/* General Styles */
body {
    font-family: 'Arial', sans-serif;
    margin: 0;
    padding: 0;
    overflow-x: hidden; /* Prevent horizontal overflow */
    background-color: #f0f8ff; /* Soft, icy blue */
    color: #2e4053; /* Deep, muted blue-gray */
}

/* Header styles */
header {
    text-align: center;
    margin-bottom: 20px;
}

h1 {
    font-size: 2.5em;
    color: #5d82a8; /* Frosty blue */
    text-shadow: 2px 2px 5px rgba(0, 0, 0, 0.2); /* Soft shadow */
    margin: 0;
}

/* Valentine's Day header text with red accents */
h2 {
    color: #d15d6a; /* Soft Valentine's Day red */
    font-style: italic;
    margin: 0;
}

/* Profile image */
.profile-img {
    width: 100%;
    max-width: 400px;
    height: auto;
    object-fit: cover;
    border: 8px solid #5d82a8; /* Frosty blue border */
    border-radius: 50%; /* Circular image for a cozy touch */
    box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.4);
    transition: transform 0.3s ease-in-out;
    margin-top: 15px;
}

.profile-img:hover {
    transform: scale(1.05); /* Gentle zoom effect on hover */
}

/* Container styling */
.container {
    width: 100%;
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 20px;
}

/* Section styling */
section {
    background: #ffffff; /* Pure white for clean elegance */
    border-radius: 12px;
    padding: 20px;
    margin: 20px 0;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.15); /* Soft shadow for depth */
    border: 1px solid #e1eff6; /* Subtle icy blue border */
    backdrop-filter: blur(5px); /* Slight blur for a frosty effect */
}

/* Valentine's Day - Hearts around list items */
ul {
    list-style-type: none;
    padding: 0;
}

ul li::before {
    content: '❤️'; /* Heart icon for a Valentine's touch */
    margin-right: 10px;
    color: #d15d6a; /* Soft Valentine's Day red */
}

/* Valentine's touch for horizontal rule */
hr {
    margin: 20px 0;
    border: 1px solid #f5b7b1; /* Soft pink */
}

/* Footer styling */
footer {
    text-align: center;
    margin-top: 20px;
    font-size: 0.9em;
    color: #5d82a8;
    background-color: #e1eff6; /* Light blue background */
    padding: 15px 0;
    border-radius: 8px;
    box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1); /* Subtle depth */
}

/* Valentine's Day accent for footer */
footer p {
    color: #d15d6a; /* Soft Valentine's Day red for footer text */
}
